.toggle {
  background-color: transparent;
  border: none;
  position: relative;
  width: 25px;
  height: 11px;
  padding: 0;
  margin-left: auto;
  transition: transform 0.3s;
  cursor: pointer;

  &::after,
  &::before {
    position: absolute;
    content: "";
    background-color: $block-background-3;
    transform: rotate(180deg);
    transition: transform 0.5s;
    width: 25px;
    height: 3px;
    border-radius: 1px;
    left: 0;
  }

  &::after {
    top: 12px;
  }

  &::before {
    top: 4px;
  }

  &--active::after {
    top: 7px;
    left: -3px;
    width: 24px;
    height: 2px;
    transform: rotate(45deg);
  }

  &--active::before {
    top: 7px;
    left: -3px;
    width: 24px;
    height: 2px;
    transform: rotate(-45deg);
  }

  @media (min-width: $tablet-width) {
    margin-left: 22px;
    margin-top: -5px;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.main-header--open .toggle::before {
  transform: rotate(45deg);
  transform-origin: 50% 50%;
  top: 4px;
}

.main-header--open .toggle::after {
  transform: rotate(-45deg);
  transform-origin: 0 100%;
  top: 13px;
  left: 4px;
}
