.user {
  background-color: $basic-white;
  border-radius: 10px;
  position: relative;
  width: 270px;
  margin-bottom: 8px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    width: 285px;
    border-radius: 20px;
  }

  &:hover {
    filter: drop-shadow(0 4px 9px rgba(0, 0, 0, 0.16));
  }
}

.main-page .user__avatar-wrapper {
  overflow: hidden;
  width: 270px;
  height: 270px;

  @media (min-width: $tablet-width) {
    width: 285px;
    height: 285px;
  }
}

.user:hover .user__avatar {
  transform: scale(1.2);
}

.user__information {
  padding: 23px 59px 17px 43px;
  display: flex;
  flex-direction: column;
}

.users-list__item {
  overflow: hidden;
}

.user__name {
  font-weight: 700;
  font-size: 21px;
  line-height: 21px;
  color: $basic-blue-light;
}

.user__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-page__user {
  background-color: $block-background-1;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 30px;
  position: relative;
  z-index: $z-user;

  @media (min-width: $tablet-width) {
    padding-top: 62px;
    padding-left: 74px;
    padding-right: 74px;
    padding-bottom: 100px;
  }

  @media (min-width: $desktop-width) {
    position: relative;
    padding-left: 124px;
    padding-top: 69px;
    padding-right: 59px;
  }
}

.user__online {
  background-color: $special-red;
  margin-right: 10px;
  margin-bottom: 17px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
}

.form-page .user__name,
.form-page .user__online {
  display: none;
}

.user__button {
  display: none;
}

.users-list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-top: 30px;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
  }

  @media (min-width: $desktop-width) {
    grid-column: 2/3;
    grid-row: 1/3;
  }
}

.about__users .users-list {
  padding-top: 54px;
  margin-bottom: 13px;

  @media (min-width: $tablet-width) {
    padding-top: 34px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.about__users .users-list__item {
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    padding-bottom: 0;
  }
}

.about__users .users-list__item:first-child {
  @media (min-width: $tablet-width) {
    margin-right: 50px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 60px;
  }
}

.about__users .user__information {
  padding: 27px 59px 25px 43px;

  @media (min-width: $tablet-width) {
    padding: 38px 59px 28px 28px;
  }

  @media (min-width: $desktop-width) {
    padding: 35px 59px 29px 28px;
  }
}

.about__users .user__name {
  margin-bottom: 14px;

  @media (min-width: $tablet-width) {
    font-weight: 700;
    font-size: 24px;
    line-height: 22px;
    margin-bottom: 24px;
  }
}

.about__users .user__avatar {
  width: 270px;
  height: 270px;

  @media (min-width: $tablet-width) {
    width: 285px;
    height: 285px;
  }
}

