.form-header__wrapper .user__avatar {
  @media (min-width: $desktop-width) {
    display: block;
    width: 190px;
    height: 190px;
    border-radius: 0;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
  }
}

.form-page__user .user__avatar {
  margin-right: 0;
  margin-bottom: 13px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-left: 17px;

  @media (min-width: $tablet-width) {
    display: block;
    border-radius: 10px;
    width: 94px;
    height: 94px;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}
