.socials-list {
  margin: 0;
  padding: 0;
  list-style: none;
  grid-column: 1 / 3;
  display: none;
}

.main-header .socials-list__item {
  margin-right: 27px;
}

.footer .socials-list__item {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: $basic-yellow;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 0;

  @media (min-width: $tablet-width) {
    width: 62px;
    height: 62px;
  }
}

.footer .socials-list {
  display: flex;
  padding-bottom: 37px;
  position: relative;
  justify-content: center;

  @media (min-width: $tablet-width) {
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    margin-left: 368px;
    margin-right: 414px;
  }
}

.footer .socials-list::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 100%;
  background-color: $lvl__divider;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.socials-list__item {
  cursor: pointer;
}

.footer .socials-list:hover {
  background-color: $special-yellow-dark;
}

.footer .socials-list:active {
  fill: 0.3;
}

.socials-list__icon {
  fill: $special-blue-1;
  width: 45px;
  height: 45px;

  @media (min-width: $tablet-width) {
    width: 62px;
    height: 62px;
  }
}

.main-header--open .socials-list {
  display: flex;
  margin-top: 30px;
  padding-left: 67px;

  @media (min-width: $tablet-width) {
    margin-top: 113px;
    padding-left: 60px;
  }
}

.main-header--open .socials-list__item {
  margin-right: 0;
}

.socials-list__item:active .socials-list__icon {
  opacity: 0.3;
}

.main-header .socials-list__item:hover {
  opacity: 0.6;
}

.main-header .socials-list__item:active {
  opacity: 0.3;
}
